import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PortfolioService } from '../portfolio.service';


@Component({
    selector: 'app-project-single',
    templateUrl: './project-single.component.html',
    styleUrls: ['./project-single.component.css']
})


export class ProjectSingleComponent implements OnInit, AfterViewInit {
    project;

    constructor(route: ActivatedRoute, Project: PortfolioService) {
        route.params.subscribe(params => {
            Project.getPortfolio()
                .subscribe(
                    (response) => {
                        this.project = this.getProject(params.name, response.json())
                        console.log(typeof this.project.thumbnails === 'object');

                    },
                    (error) => console.log(error)
                )
        });
    }

    ngOnInit() {}

    ngAfterViewInit() {
        $(document).ready(function(){
            (<any>$("a.thumbnail")).fancybox({
                buttons: [
                    "share",
                    "slideShow",
                    "fullScreen",
                    "thumbs",
                    "close"
                ],
                thumbs : {
                    autoStart : true
                }
            });
        });
    }

    getProject(name, data) {
        return data.filter(function (obj) {
            return obj.route === name;
        })[0];
    }

    isObject(val) {
        return typeof val === 'object';
    }

}
