import { BrowserModule }          from '@angular/platform-browser';
import { NgModule }               from '@angular/core';
import { RouterModule, Routes }   from '@angular/router';
import { HttpModule }             from '@angular/http';

import { AppComponent }           from './app.component';
import { PageNotFoundComponent }  from './page-not-found/page-not-found.component';
import { HomeComponent }          from './home/home.component';
import { SidebarLeftComponent }   from './sidebar-left/sidebar-left.component';
import { ProjectsComponent }      from './projects/projects.component';
import { ProjectComponent }       from './project/project.component';
import { AboutComponent }         from './about/about.component';
import { ProjectSingleComponent } from './project-single/project-single.component';
import { AwardsComponent }        from './awards/awards.component';
import { AwardSingleComponent }   from './award-single/award-single.component';

import { PortfolioService }       from './portfolio.service';
import { CertService }            from './cert.service';
import { TermsConditionsComponent } from './terms-conditions/terms-conditions.component';

import * as $ from 'jquery';

const appRoutes: Routes = [
  {
    path: '',
    component: HomeComponent,
    data: { title: 'Home' }
  },
  {
    path: 'projects',
    component: ProjectsComponent,
    data: { title: 'Projects' }
  },
  {
    path: 'project/:name',
    component: ProjectSingleComponent
  },
  {
    path: 'awards',
    component: AwardsComponent
  },
  {
    path: 'award/:name',
    component: AwardSingleComponent,
  },
  {
    path: 'about',
    component: AboutComponent,
    data: { title: 'About' }
  },
  {
    path: 'terms-and-conditions',
    component: TermsConditionsComponent,
    data: { title: 'About' }
  },
  { path: '**', component: PageNotFoundComponent }
];



@NgModule({
  declarations: [
    AppComponent,
    PageNotFoundComponent,
    HomeComponent,
    SidebarLeftComponent,
    ProjectsComponent,
    ProjectComponent,
    AboutComponent,
    ProjectSingleComponent,
    AwardsComponent,
    AwardSingleComponent,
    TermsConditionsComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(appRoutes,{useHash: true}),
    HttpModule
  ],
  providers: [PortfolioService,CertService],
  bootstrap: [AppComponent]
})
export class AppModule { }

