import { Component, OnInit } from '@angular/core';
import { ActivatedRoute }    from '@angular/router';
import { CertService }       from '../cert.service';

@Component({
  selector: 'app-award-single',
  templateUrl: './award-single.component.html',
  styleUrls: ['./award-single.component.css']
})
export class AwardSingleComponent implements OnInit {
  Cert;

  constructor(route: ActivatedRoute, CertService: CertService) { 
    route.params.subscribe(params => {
      console.log(params.name);
      CertService.getCert().subscribe(
        (response) => {
          this.Cert = this.getData(params.name,response.json())
        },
        (error) => console.log(error)
      )
    });
  }

  ngOnInit() {  }

  getData(name,data) {
    return data.filter(function (obj) {
      return obj.route === name;
    })[0];
  }

}
